.background {
    width: 100%;
    height: 50rem;
    /* background: linear-gradient
    (90deg, rgba(1, 1, 5, 1) 0%, rgba(16, 18, 89, 1) 80%, rgba(0, 212, 255, 1) 100%); */
    margin-top: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero {
    text-align: center;
    margin-top: -8rem;
  }
  
  .hero span {
    color: #00d4ff;
  }
  
  .introduction {
    margin-top: 4rem;
  }
  
  .my-self {
    text-align: center;
  }
  
  .self-title {
    letter-spacing: 1px;
    color: rgb(17, 13, 1);
    font-size: 2em;
    font-family: 'Ultra', sans-serif; /* Apply the 'Ultra' font */
    transition: color 0.3s ease-in-out;


  }
  
  .self-title:hover {
    color: rgb(235, 176, 66);

  }
  .self-description {
    font-size: 1.5rem;
    color: #b0b0b0;
    line-height: 1;
    max-width: 600px;
    margin: 0 auto;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-weight:lighter;
  }
  
  .img-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
  }
  
  .img-wrapper img {
    width: 200px;
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    transition: 0.5s ease-out 100ms;
    cursor: pointer;
    border-radius: 10px; /* Add border radius */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Add shadow */


  }
  
  .img1 {
    height: 20rem;
    margin-right: -35px;
    display: flex;
    justify-content: center;
    align-items: center;

  }
  
  .img2 {
    height: 25rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .img3 {
    height: 20rem;
    margin-left: -35px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  
  .img1 img,
  .img2 img,
  .img3 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px; /* Add border radius */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Add shadow */
  }
  